<template>
    <div class="Display">
        <p class="Display__value">
            {{ displayedValue || '00:00' }}
        </p>
    </div>    
</template>

<script>
import {computed} from 'vue';

export default {
    name: "Display",
    props: {
        breakIsActive: {
            type: Boolean,
            required: true
        },
        currentBreakTime: {
            type: String,
            required: true
        },
        currentSessionTime: {
            type: String,
            required: true
        }
    },
    setup(props) {
        const displayedValue = computed(function() {
            return props.breakIsActive ? props.currentBreakTime : props.currentSessionTime;
        })
        return {displayedValue};
    }
}
</script>

<style scoped>
* {
    --bgcColor: #1E213A;
}
.Display {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-grow: 1;
    width: 100%;

    background-color: var(--bgcColor);
    border-radius: 0 0 5px 5px;

    transition: all .3s ease-in-out;
}

.Display__value {
    width: 100%;

    text-align: center;

    font-size: 5.5rem;
    font-weight: bold;
    
    color: white;

    user-select: none;
}

.Display--break {
    background-color: #28df99;
}

/* For Galaxy Fold */
@media (min-width: 653px) and (max-height: 299px) {
    .Display {
        max-height: 600px;
    }

    .Display__value {
        font-size: 3rem;
    }
}

@media (min-width: 768px) {
    .Display {
        width: 90%;
        max-height: 600px;
    }

    .Display__value {
        font-size: 7.8rem;
    }
}

@media (min-width: 992px) {
    .Display {
        width: 800px;
    }

    .Display__value {
        font-size: 12rem;
    }
}

</style>
